.star{
  -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  background: black;
  position: relative;  
  height: 150px;
  width: 350px;
  
  }
  .left{
    -webkit-clip-path: polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%);
    clip-path: polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%);
    background: crimson;
    width: 150px;
    height: 100px;
    z-index:20;
    -webkit-animation: colorchange 5s;
            animation: colorchange 5s; 
  }
  .right{
    -webkit-clip-path: polygon(40% 0%, 40% 20%, 100% 20%, 100% 80%, 40% 80%, 40% 100%, 0% 50%);
    clip-path: polygon(40% 0%, 40% 20%, 100% 20%, 100% 80%, 40% 80%, 40% 100%, 0% 50%);
    background: crimson;
    width: 150px;
    height: 100px;
    -webkit-animation: colorchange 5s;
            animation: colorchange 5s; 
  }
  @-webkit-keyframes colorchange
  {
     0%   {background: white;}
    10%  {background: crimson;}
    20%  {background: white;}
    30%  {background: crimson;}
    40% {background: white;}
    50% {background: crimson;}
    60% {background: white;}
    70% {background: crimson;}
    80% {background: white;}
    90% {background: crimson;}
   
  }
  @keyframes colorchange
  {
     0%   {background: white;}
    10%  {background: crimson;}
    20%  {background: white;}
    30%  {background: crimson;}
    40% {background: white;}
    50% {background: crimson;}
    60% {background: white;}
    70% {background: crimson;}
    80% {background: white;}
    90% {background: crimson;}
   
  }
  .PizzaMakerFont{
    position:absolute; 
    top:50%; 
    left:50%;
    color:black;
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
    text-align:center;
    -webkit-animation: fontchange 5s;
            animation: fontchange 5s; 
  }
  @-webkit-keyframes fontchange
  {
     0%   {color: white;}
    10%  {color: black;}
    20%  {color: white;}
    30%  {color: black;}
    40% {color: white;}
    50% {color: black;}
    60% {color: white;}
    70% {color: black;}
    80% {color: white;}
    90% {color: black;}
  }
  @keyframes fontchange
  {
     0%   {color: white;}
    10%  {color: black;}
    20%  {color: white;}
    30%  {color: black;}
    40% {color: white;}
    50% {color: black;}
    60% {color: white;}
    70% {color: black;}
    80% {color: white;}
    90% {color: black;}
  }
  .blueShape {
    background: -webkit-linear-gradient(45deg, black 20%, dodgerBlue 90%);
    background: linear-gradient(45deg, black 20%, dodgerBlue 90%);
    width:582px;
    height: 230px;
    -webkit-clip-path: polygon(0 0, 100% 0%, 60% 100%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, 80% 100%, 0% 100%);
    color:white;
    -webkit-animation: fadein 2s;
